import Highcharts from "highcharts";
import HighchartsMore from "highcharts/highcharts-more";
import HighchartsReact from "highcharts-react-official";

HighchartsMore(Highcharts);

const skills = [
    { category: "Algorithms", score: 2.5 },
    { category: "Architecture", score: 4.5 },
    { category: "Databases", score: 3.5 },
    { category: "Frameworks", score: 4.5 },
    { category: "Infrastructure", score: 3.0 },
    { category: "Mobile", score: 2.5 },
    { category: "UI/UX", score: 4.0 },
    { category: "Web", score: 4.5 },
];

export default function RadarChartView() {
    return (
        <HighchartsReact
            highcharts={Highcharts}
            options={{
                chart: {
                    height: 280,
                    polar: true,
                    type: "line",
                    width: 360,
                },
                title: {
                    text: null,
                    x: 0,
                },
                xAxis: {
                    categories: skills.map((s) => s.category),
                    labels: {
                        style: {
                            color: "#222",
                        },
                    },
                    gridLineColor: "#ccc",
                    tickmarkPlacement: "on",
                    lineWidth: 0,
                },
                yAxis: {
                    gridLineColor: "#ccc",
                    gridLineInterpolation: "polygon",
                    lineWidth: 0,
                    max: 5,
                    min: 0,
                    labels: {
                        enabled: false,
                    },
                },
                series: [
                    {
                        name: "Skills",
                        data: skills.map((s) => s.score),
                        pointPlacement: "on",
                    },
                ],
                plotOptions: {
                    series: {
                        tooltip: {
                            pointFormatter: function () {
                                const value = this.y;
                                console.log(this);
                                let rank = "N/A";
                                if (value <= 1) {
                                    rank = "Learning";
                                } else if (value <= 2) {
                                    rank = "Beginner";
                                } else if (value <= 3) {
                                    rank = "Intermediate";
                                } else if (value <= 4) {
                                    rank = "Advanced";
                                } else {
                                    rank = "Expert";
                                }
                                return `<b>${rank}</b>`;
                            },
                        },
                    },
                },
                legend: {
                    enabled: false,
                },
                tooltip: {
                    enabled: false,
                },
                credits: {
                    enabled: false,
                },
                exporting: {
                    enabled: false,
                },
            }}
        />
    );
}
