import React from "react";
import { Grid, LinearProgress, Tooltip } from "@material-ui/core";
import { createStyles, makeStyles, Theme, withStyles } from "@material-ui/core/styles";

const traits = [
    {
        name: "Mind",
        description: "how we interact with our environment.",
        edges: [
            { label: "Extraverted", value: 40 },
            { label: "Intraverted", value: 60 },
        ],
        link: "https://www.16personalities.com/articles/mind-introverted-vs-extraverted",
    },
    {
        name: "Energy",
        description: "where we direct our mental energy.",
        edges: [
            { label: "Intuitive", value: 39 },
            { label: "Observant", value: 61 },
        ],
        link: "https://www.16personalities.com/articles/energy-intuitive-vs-observant",
    },
    {
        name: "Nature",
        description: "how we make decisions and cope with emotions.",
        edges: [
            { label: "Thinking", value: 61 },
            { label: "Feeling", value: 39 },
        ],
        link: "https://www.16personalities.com/articles/nature-thinking-vs-feeling",
    },
    {
        name: "Tactics",
        description: "our approach to work, planning and decision-making.",
        edges: [
            { label: "Judging", value: 74 },
            { label: "Prospecting", value: 26 },
        ],
        link: "https://www.16personalities.com/articles/tactics-judging-vs-prospecting",
    },
    {
        name: "Identity",
        description: "how confident we are in our abilities and decisions.",
        edges: [
            { label: "Assertive", value: 81 },
            { label: "Turbulent", value: 19 },
        ],
        link: "https://www.16personalities.com/articles/identity-assertive-vs-turbulent",
    },
];

const useStyles = makeStyles((t: Theme) =>
    createStyles({
        trait: {
            color: t.palette.text.secondary,
            paddingBottom: t.spacing(0.25),
        },
    })
);

interface Props {}

export default function PersonalityView(props: Props) {
    const classes = useStyles(props);

    return (
        <div>
            {traits.map(({ description, edges, link, name }, idx) => {
                const [e1, e2] = edges;
                let [leftColor, rightColor] = ["#f0f0f0", "#008adb"];

                if (e1.value > e2.value) {
                    [leftColor, rightColor] = [rightColor, leftColor];
                }
                const ProgressBar = withStyles((t: Theme) => {
                    return createStyles({
                        bar: {
                            backgroundColor: leftColor,
                        },
                        colorPrimary: {
                            backgroundColor: rightColor,
                        },
                        root: {
                            borderRadius: t.spacing(2.5),
                            height: 20,
                        },
                    });
                })(LinearProgress);

                return (
                    <Grid key={idx} container className={classes.trait} spacing={2}>
                        <Grid item xs={3} style={{ textAlign: "right" }}>
                            {edges[0].label} {e1.value}%
                        </Grid>
                        <Grid item xs={6} style={{ cursor: "pointer" }} onClick={() => window.open(link, "_blank")}>
                            <Tooltip title={`${name}: ${description}`}>
                                <ProgressBar variant="determinate" value={e1.value} />
                            </Tooltip>
                        </Grid>
                        <Grid item xs={3}>
                            {e2.value}% {edges[1].label}
                        </Grid>
                    </Grid>
                );
            })}
        </div>
    );
}
