import React from "react";

import { createStyles, makeStyles } from "@material-ui/core/styles";

import WordCloud from "react-d3-cloud";

import links from "./links";

const skills = [
    // Languages
    "Python",
    "JavaScript",
    "Java",
    "Groovy",
    "Go",
    "Kotlin",
    "Swift",
    "Objective-C",
    "Ruby",
    "Scala",

    // Frameworks
    "Django",
    "Spring",
    "React",
    "Vue",
    "Celery",
    "Node.js",
    "GraphQL",
    "Hibernate",
    "Drools",
    "Rails",

    // Platforms & Tools
    "AWS",
    "iOS",
    "Android",
    "Docker",
    "PostgreSQL",
    "MongoDB",
    "MySQL",
    "Redis",
    "Memcached",
    "Jenkins",
    "Cypress",
    "Nginx",
    "Keycloak",
    "Ansible",
    "Gradle",
    "Maven",
];

const useStyles = makeStyles((theme) =>
    createStyles({
        root: {
            "& text": {
                cursor: "pointer",
            },
        },
    })
);

export default function TagCloudView(props) {
    const classes = useStyles(props);

    return (
        <div className={classes.root}>
            <WordCloud
                data={skills.reverse().map((item, idx) => {
                    return {
                        text: item,
                        value: idx + 1,
                    };
                })}
                height={300}
                font="Impact"
                fontSizeMapper={(word) => Math.log2(word.value) * 8}
                padding={2}
                onWordClick={(word) => {
                    const link = links[word.text];
                    if (link) {
                        window.open(link, "_blank");
                    }
                }}
                rotate={(word) => ~~(Math.random() * 2) * 90}
            />
        </div>
    );
}
